import { freeze } from '../util/object-utils.js';
/**
 * @internal
 */
export const FilterNode = freeze({
    is(node) {
        return node.kind === 'FilterNode';
    },
    create(left, op, right) {
        return freeze({
            kind: 'FilterNode',
            left,
            op,
            right,
        });
    },
});
